@media(max-width:425px){
    .contenedor-card-marketplace{
        padding: 10px;
        
        background-image: linear-gradient(108.9deg, #201f1f 4.9%, #3e3e3e 97%);
        
        
      
        border-radius: 20px;
        box-sizing: content-box;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        .contenedor-img-card{
            img{
                width: 150px;
                margin:30px;
            }
        }
        .contenedor-text-card{
           margin-left: 10px;
            display: flex;
            flex-direction: column;
            align-items: center;
            .puntuacion-jugador{
                background: rgb(2,0,36);
                background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(171,243,107,1) 0%, rgba(180,243,107,1) 21%, rgba(202,243,107,1) 100%);
                width: 100%;
                padding: 5px 5px;
                border-radius: 15px;
                text-align: center;
                font-weight: bolder;
                color: var(--darkGrey);
                font-size: 0.8rem;
            }
            .jugador-name{
                font-size: 1rem;
                font-weight: 800;
                padding: 5px 0px;
            }
            .contenedor-buttons{
              width: 100%;
              display: flex;
              align-items: center;
              justify-content: center;
              button{
                  background-color: transparent;
                  padding: 10px 20px;
                  font-size: 0.7rem;
                  border: 1px solid white; 
                  color: white;
                  font-weight: 800;
                  border-radius: 10px;
              }
              :nth-child(2n) {
                margin-left: 10px;
              }
            }
            p{
                margin-top: 10px;
                font-size: 0.8rem;
                text-align: center;
            }
            .contenedor-price-buynow{
                width: 100%;
                display: flex;
                justify-content: space-between;
              
                .contendor-price{
                    h3{
                        font-size: 0.7rem ;
                        font-weight: 900;
                    }
                    h4{
                        font-size: 0.5rem;
                    }
                }
                .contenedor-buy{
                    button{
                        background: rgb(255,255,255);
                        background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 0%, rgba(243,212,107,1) 0%, rgba(201,173,76,1) 100%);
                        border: none;
                        border-radius: 10px;
                        padding: 10px 20px;
                        color: white;
                        font-weight: 800;
                        font-size: .6rem;
                    }
                }
                .contenedor-button-config{
                    button{
                        background-color: transparent;
                        padding: 5px 10px;
                        border: 1px solid white; 
                        color: white;
                        font-weight: 800;
                        border-radius: 10px;
                        img{
                            width: 10px;
                        }
                    }
                }
            }
        }
    }
}
