@media(max-width:1024px){

    .contenedor-marketplace{
        margin-top: 150px;
        position: relative;
        
        .contenedor-background-market{
            position: absolute;
            top: -20px;
        }
        h2 {
            -webkit-text-stroke: 2px #6BF394; 
            color: rgba($color: #6BF394, $alpha: 0.2);
            font-size: 1.5rem;
            font-family: var(--fontSecundaria);
            text-align: center;
        }
       
        .contenedor-info-marketplace{
            
            margin-top: 80px;
            padding: 0 14px;
            .contenedor-text-marketplace{
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;

                h3{
                    color: var(--ligthbrown);
                    font-size: 1.3rem;
                    font-weight: 800;
                }
                p{
                    line-height: 1.7rem;
                    text-align: center;
                }
            }
        }
        
    }

}
    

