@font-face {
  font-family: 'TT Firs Neue';
  
  src: url('./assets/TTFrisNeue/TTFirsNeue-Regular.eot');
  src: local('TT Firs Neue Regular'), local('TTFirsNeue-Regular'),
      url('./assets/TTFrisNeue/TTFirsNeue-Regular.eot?#iefix') format('embedded-opentype'),
      url('./assets/TTFrisNeue/TTFirsNeue-Regular.woff2') format('woff2'),
      url('./assets/TTFrisNeue/TTFirsNeue-Regular.woff') format('woff'),
      url('./assets/TTFrisNeue/TTFirsNeue-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'TT Firs Neue';
  src: url('./assets/TTFrisNeue/TTFirsNeue-Bold.eot');
  src: local('TT Firs Neue Bold'), local('TTFirsNeue-Bold'),
      url('./assets/TTFrisNeue/TTFirsNeue-Bold.eot?#iefix') format('embedded-opentype'),
      url('./assets/TTFrisNeue/TTFirsNeue-Bold.woff2') format('woff2'),
      url('./assets/TTFrisNeue/TTFirsNeue-Bold.woff') format('woff'),
      url('./assets/TTFrisNeue/TTFirsNeue-Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}

:root{
  --background:#121214;
  --background2:#383d51;
 
  --fontcolor:·#f1f1f3;
  --fontPrincipal:'TT Firs Neue';
  
  // --fontPrincipal:'Open Sans', sans-serif;
  --fontSecundaria: 'Syncopate', sans-serif;
  --ligthGreen : #6BF394;
  --darkGrey:#303538;
  --ligthbrown:#C9A555;
  font-size: 16px;
}


* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  background-color: #000000;
  color: #f1f1f3;
  margin: 0;
  padding: 0px;
  font-family: var(--fontPrincipal);
  box-sizing: border-box;
  width: 100%;
  letter-spacing: 0.3;
  overflow-x: hidden;
}
.contenedor-app{
  margin-top: -15px;
  
}
.video-background{
  position: absolute;
  width: 100%;
  
  z-index: -10000;
  opacity: 0.4;
}
.titulo-seccion {
  -webkit-text-stroke: 2px #6BF394; 
  color: rgba($color: #6BF394, $alpha: 0.2);
  font-size: 4rem;
  font-family: var(--fontSecundaria);
  text-align: center;
  text-transform: uppercase;
}

.contenedor-background-roadmap{
  background-image: url(assets/background-roadmap.svg);
  margin-top: 80px;
  overflow: hidden;

}


.btn-grad {background: linear-gradient(90.85deg, #6BF394 5.54%, #99FFB8 95.49%);
  box-shadow: 0px 3px 30px rgba(73, 216, 116, 0.4);}
.btn-grad {
  margin: 20px 0px;
  padding: 10px 20px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: black;            
  box-shadow: 0 0 2px #eee;
  border-radius: 10px;
  display: block;
  font-weight: bolder;
  font-size: 0.6rem;
}
.btn-grad:hover {
  background-position: right center; /* change the direction of the change here */
  color: black;
  text-decoration: none;
}


.contenedor-background,.contenedor-mision,.container,.contenedor-background-roadmap{
  overflow-x: hidden; 
 }
.btn-grad-wallet {background-image: linear-gradient(to right, #212529 0%, #212529 51%, #212529 100%);}
// .btn-grad-wallet {background-image: linear-gradient(to right, #1F1C2C 0%, #6f6c7f  51%, #1F1C2C  100%)}
.btn-grad-wallet {
   margin: 20px 0px;
   padding: 5px 10px;
   text-align: center;
   text-transform: uppercase;
   transition: 0.5s;
   background-size: 200% auto;
   color: black;
   font-weight: 900;            
   box-shadow: 0 0 5px #eee;
   border-radius: 10px;
   display: block;
   
 }

 .btn-grad-wallet:hover {
   background-position: right center; /* change the direction of the change here */
   color: #fff;
   text-decoration: none;
 }
 .contenedor-background-market{
   position: absolute;

   img{
     opacity: 0.9;

   }
  
 
}


.contenedor-wallets{
  position: relative;
  text-align: center;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  
  p{
    color: white;
    text-align: center;
    margin-left: 30px;
  }
  img{
    position: absolute;
    right: 30px;
    top: 30px;
  }

}
.titulo-modal{
  font-size: 1.5rem;
}
.parrafo-modal{
  font-size: 1rem;
}
.titulo-comming{
  font-size: 1.2rem;
  font-weight: 900;
}
.contenedor-background-marketplace{
  position: relative;
  width: 100%;
 
  img{
    position: absolute;
    width: 100vw;
    top: 100px;
  }
}
.contenedor-background-tokenomics{
  position: relative;
  width: 100%;
  img{
    position: absolute;
    width: 100vw;
    z-index: -1000000000;
    top: 500px;
  }
}

@media(min-width:968px){
  .contenedor-background-marketplace{
    position: relative;
    width: 100%;
    
    img{
      position: absolute;
      width: 100vw;
      top: -500px;
    }
   
              
  }
  
}


@media (min-width:1025px) {
  .contenedor-background-tokenomics{
  position: relative;   
  width: 100%;
   
  img{
    position: absolute;
    width: 100vw;
    top: -100px;
  }


  }
  .contenedor-background-mision{
    position: relative;
    &:before{
      
        content: url('./assets/backmision.png');
        display: block;
        position: absolute;
        z-index: 1000000000000;
        top: 900px;
        right: 0px;
        opacity: 0.8;
       
        
      
    }
  }
}
@media(min-width:1024px){
  .contenedor-background-roadmap{
  margin-top: 80px;
  overflow: hidden;
  min-height: 1500px;
  background-image: url(assets/background-roadmap.svg);
  background-size: cover;
  
  
}}

@media(min-width:1025){
 
  .btn-grad {
    margin: 10px;
    padding: 15px 45px;
    text-align: center;
    text-transform: uppercase;
    transition: 0.5s;
    background-size: 200% auto;
    color: black;            
    
    border-radius: 10px;
    display: block;
    font-weight: bolder;
    font-size: 1.2rem;
  }
  
 
}